<template>
  <div id="home">
  <div class="header j-s-a">
  <div class="left">
    <label for="">/</label>
    <input type="text">
    <span class="btn"></span>
  </div>
  <div class="right">
    <span class="btn" @click="$router.push({name:'addAccount'})"></span>
  </div>
  </div>

  <div class="table">
     <a-table :row-selection="rowSelection" :columns="columns" :data-source="accountList">
    <a slot="name" slot-scope="text">{{ text }}</a>
  </a-table>
  </div>
   
  </div>
</template>

<script>
import Vue from "vue";
import main from '../../router/module/mainRouter'
import { Table, Menu, Icon, Breadcrumb } from "ant-design-vue";
import { mapState } from 'vuex';
Vue.use(Table);
const columns = [
  {
    title: '',
    dataIndex: 'key',
    scopedSlots: { customRender: 'name' },
  },
    {
    title: '',
    dataIndex: 'user',
  },
  {
    title: '',
    dataIndex: 'name',
  },
  {
    title: '',
    dataIndex: 'phone',
  },
   {
    title: '',
    dataIndex: 'job',
  },
   {
    title: '',
    dataIndex: 'auth',
  },
   {
    title: '',
    dataIndex: 'time',
  },
   {
    title: '',
    dataIndex: 'edit',
  },
];
const data = [
  {
    key: '1',
    name: 'John Brown',
    user:'admin',
    phone:13766345195,
    age: 32,
    address: 'New York No. 1 Lake Park',
    job:'',
    auth:'/',
    time:'2020-09-05 9:00',
    edit:'',
  },
  {
    key: '2',
    name: 'Jim Green',
    age: 42,
    time:'2020-09-05 9:00',
    phone:13766345195,
    auth:'/',
     user:'admin',
    address: 'London No. 1 Lake Park',
    job:'',
    edit:'',
  },
  {
    key: '3',
    name: 'Joe Black',
    phone:13766345195,
    time:'2020-09-05 9:00',
    age: 32,
     user:'admin',
    address: 'Sidney No. 1 Lake Park',
    job:'',
    auth:'/',
    edit:'',
  },
  {
    key: '4',
    name: 'Joe Black',
    age: 32,
    time:'2020-09-05 9:00',
     user:'admin',
     phone:13766345195,
     edit:'',
    address: 'Sidney No. 1 Lake Park',
    job:'',
    auth:'/'
  },
 
];
export default {
  data() {
    return {
      data,
      columns,
    };
  },
    created() {
    
    this.$store.dispatch('account/getAccount')
  },
  computed: {
    ...mapState({
      account:state => state.account.account
    }),
    accountList(){
      let arr = JSON.parse(JSON.stringify(this.account))
        arr.forEach(item => {
          item.edit = ''
        item.auth =   item.auth.map(auth => {
            let title = ''
            main.children.forEach(router => {
              if(router.name === auth){
                title = router.meta.title
              }else if(router.meta.hasSub){
                const result = router.children.find(sub => sub.name === auth)
                if(result){
                  title = result.meta.title
                }
              }
            })
            
            return title
            
            
          }).join(' / ')
        })
        return arr
    },
    rowSelection() {
      return {
        onChange: (selectedRowKeys, selectedRows) => {
          
        },
        getCheckboxProps: record => ({
          props: {
            disabled: record.name === 'Disabled User', // Column configuration not to be checked
            name: record.name,
          },
        }),
      };
    },
  },
};
</script>

<style lang="scss" scoped>
    .left{
        display: flex;
        align-items: center;
        input{
            margin:  0 20px;
            border:none;
            border: 1px #ccc solid;
            outline: none;
            height: 30px;
        }
    }
    .table{
      margin-top: 40px;
    }
    .btn{
        cursor: pointer;
        display: inline-block;
        height: 30px;
        width: 80px;
        border-radius: 10px;
        background-color: #2D8CF0;
        color: #fff;

        text-align: center;
    }
</style>